@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

html, body {
  height: 100vh;
}

.main {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 1.8rem;
}

#root {
  width: 100vw;
}

.login-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

nav {
  overflow: auto;
}

span {
  font-size: .8rem;
}

img { margin-left: 40px;}


